
.user-container{
    
    width: 100%;
    margin:10px;
    cursor: pointer;
    background-color: white;
    display: flex;
    border-radius: 25px;
    transition: 0.3s;
   
}
.user-container:hover{
    box-shadow: 0 10px 10px #c9c9c9;
}
.image-wrapper{
    width: 100px;
    padding: 30px;

}
.imageHolder{
    width: 100%;
    border-radius: 50%;
    
}
.user-detail-container{
    width: 100%;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
   
}

/* style for user description  */
.user-detail-container *{
    width: 100%;
}
.name{
    font-size: 30px;
    font-weight: bolder;
}
.city{
    font-weight: 200;
    font-style: italic;
    color: gray;
}